.header{
    padding: 45px 15px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo_con{
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    top: 30px;
    left: 25px;
    font-size: 36px;
    color: white;
    text-decoration: none;
}
.logo_con img{
    height: 50px;
    position: absolute;
    filter: invert(1);
    top: -18px;
    left: -6px;
}
.p_l{
    color: #30cfd0;
    font-size: 50px;
    position: relative;
}
.main{
    display: flex;
    flex-direction: row;
   /* position: sticky;
    position: -webkit-sticky;
    top: 0;
    left: 0;*/
}


.el{
    padding: 10px 30px;
    background-color: rgba(0, 0, 0, 0.54);
    font-size: 18px;
    color: white;
}

.el a{
    color: #fff;
    text-decoration: none;
    transition: all 0.3s ease;
}
.el a:hover{
    color: #91a6ff;
}

.el:first-child{
    border-bottom-left-radius: 20px;
    border-top-left-radius: 20px;
}
.el:last-child{
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
}
.activeLink{
    color: #f43b47!important;
}
.menu{
    display: none;
    margin-top: 5px;
    cursor: pointer;
}
.palka{
    display: block;
    height: 5px;
    width: 45px;
    background-color: white;
    border-radius: 5px;
    transition: all 0.3s ease;
}
.palka:not(:last-child){
    margin-bottom: 8px;
}
.menu:hover .palka{
    opacity: 0.8;
}
.hidden_menu{
    width: 300px;
    position: absolute;
    right: -300px;
    transition: all 0.8s ease;
    top: 0;
    /*background-color: #0000FF;*/
    /*background-image: linear-gradient(to top, #09203f 0%, #537895 100%);*/
    background-image: linear-gradient(to top, #1e3c72 0%, #1e3c72 1%, #2a5298 100%);
    height: 100vh;
    display: none;
    flex-direction: column;
    padding-top: 65px;
    z-index: 10;
}
.close_menu{
    position: absolute;
    right: 10px;
    top: 20px;
    font-size: 15px;
    color: white;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.close_menu span{
    font-size: 20px;
    margin-left: 20px;
    border: 1px solid #fff;
    height: 30px;
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    cursor: pointer;
    border-radius: 8px;
    transition: all 0.3s ease;
}
.close_menu span:hover{
    background-color: #ff0000;
}
.hidden_menu_el{
    color: #fff;
    margin-top: 15px;
    margin-left: 30px;
    text-decoration: none;
    font-size: 24px;
}


@media (max-width: 1200px) {
    .header{
        justify-content: flex-end;

    }
}
@media (max-width: 870px) {
    .main{
        display: none;
    }
    .menu{
        display: block;
    }
}



















