.container{
    max-width: 800px;
    margin: 0 auto;
    padding: 0 10px;
    width: 100%;
}

.main{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    color: #fff;
    margin-top: 30px;
    padding: 20px 0;
}
.con_1{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.p_l{
    position: relative;
    color: #30cfd0;
    font-size: 36px;
    text-decoration: none;
    margin-right: 10px;
}
.p_l img{
    position: absolute;
    top: -10px;
    left: -3px;
    height: 30px;
    filter: invert(1);
}
.network{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.network img{
    height: 40px;
    filter: invert(1);
    transition: all 0.3s ease;
    border-radius: 50%;
}
.network a:not(:last-child){
    margin-right: 10px;
}


.network a:hover .shest{
    filter: invert(1) brightness(40%)
}

.network a:hover .vk{
    background-color: #a6825c;
}

.network a:hover .you{
    background-color: #3bcfd4;
}
.network a:hover .tele{
    background-color: #ff7733;
}

@media (max-width: 470px) {
    .footer {

        flex-direction: column;

    }
}


