.elem_img{
    width: 250px;
    height: 360px;
}

.main_el{
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    order: 2;
}
.elem{
    margin: 10px;
    position: relative;
    width: 250px;
    height: 360px;
}
.elem:hover .elem_name{
    /*background-image: linear-gradient(to right, rgba(67, 233, 123, 1) 0%, rgba(56, 249, 215, 1) 100%);*/
    background-color: rgba(0, 0, 0, 1);
}
.elem:hover .elem_img{
    filter: blur(1px);
}
.elem_name{
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
    width: 100%;
    max-width: 250px;
    /*background-image: linear-gradient(to right, rgba(67, 233, 123, 0.7) 0%, rgba(56, 249, 215, 0.7) 100%);*/
    background-color: rgba(0, 0, 0, 0.7);
    padding: 5px 0;
    transition: all 0.3s ease;
    /*background-image: linear-gradient(to top, #fcc5e4 0%, #fda34b 15%, #ff7882 35%, #c8699e 52%, #7046aa 71%, #0c1db8 87%, #020f75 100%);*/
}
.elem_h3{
    text-wrap: unset;
    word-wrap: break-word;
    color: #fff;
}

.elem_ear{
    position: absolute;
    top: 0;
    right: 0;
    color: #fff;
    padding: 5px;
    background-color: black;
   /* background-color:   #38f9d7;*/
}
.container{
    margin: 0 auto;
    max-width: 1500px;
    padding: 0 10px;
}

.Search{
    width: 100%;
    margin: 10px;
}
.Search_in{
    width: 100%;
    margin-bottom: 50px;
}

.Search_form{
    width: 100%;
    display: flex;
    justify-content: center;
}
.searh_pop{
    position: relative;
    width:  90%;
    display: flex;
    justify-content: center;
}
.Search_input{
    width: 100%;
    height: 50px;
    font-size: 36px;
    border-radius: 25px ;
    border: none;
    padding-left: 20px;
    outline: none;
}
.Search_submit{
    position: absolute;
    top: 10px;
    right: 15px;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
}
.Search_submit img{
    height: 30px;
}

.main{
    display: flex;
    flex-direction: row;
}
.genres{
    order: 1;
    display: flex;
    justify-content: center;
    height: 100%;

}
.genres_con{
    width: 350px;
    background-color: white;
    border-radius: 25px;
}
.genres_con h2{
    padding: 20px 10px 0 10px;
}
.genres_con_a{
    padding: 20px 10px;
    display: flex;

    flex-wrap: wrap;
}
.genres_con a{
    color: #0000FF;
    text-decoration: none;
    margin: 5px;
}
.genres_con a:hover{
    text-decoration: underline;
}

.notFound{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 25px;
    background-color: #fff;
    font-size: 30px;
    height: 60px;
    margin: 0 70px;
}
@media (max-width: 500px) {
    .main_el{
        justify-content: center;
    }
    .genres{
        width: 300px!important;
    }
}
@media (max-width: 850px) {
    .main{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .main_el{
        order: 2;
        justify-content: center;
    }
    .genres{
        order: 1;
        width: 430px;
        margin-bottom: 30px;
    }
}

