.container{
    max-width: 1500px;
    margin: 0 auto;
    padding: 0 10px;
}
.main{
    display: flex;
    flex-direction: column;
}
.elem{
    background-color: #fff;
    border-radius: 25px;
    display: flex;
    margin-bottom: 15px;
}
.elem img{
    height: 200px;
    border-bottom-left-radius: 25px;
    border-top-left-radius: 25px;
}

.podpipi{
    padding: 10px;
}


@media (max-width: 700px) {
    .elem img{
        display: none;
    }
}














