.container{
    max-width: 1500px;
    margin: 0 auto;
    padding: 0 10px;
}
.main{
    background-color: #fff;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    margin: 15px 0;
}

.row_1{
    display: flex;
    margin-bottom: 30px;
}
.row_1 img{
    width: 350px;
}

.row_1_name{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    width: 100%;
    padding: 15px;
}
.row_1_name_1{
    display: flex;
    flex-direction: column;
}

.row_1_2_apl{

}
.row_1_2_apl span{
    font-size: 30px;
}
.row_1_name_1 h1{
    margin-bottom: 10px;
}
.row_1_name_1 p{
    margin-bottom: 10px;
}
.row_1_name_1 a{
    font-size: 20px;
}

.row_2_dis{
    margin-bottom: 30px;
}
.row_2_dis p{
    padding: 5px;
}

.film{
    width: 100%;

    margin-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.film h2{
    margin-bottom: 10px;
}
.proigrvatel{
    width: 100%;
    max-width: 1000px;
    display: block;
    height: 600px;
    background-color: #0000FF;
}
.row_1_genres_befor a{
    text-decoration: none;
}
.row_1_name_1 a{
    text-decoration: none;
}

 .row_1_name_1 a:visited{
     color: #0000FF;
 }

@media (max-width: 900px) {
    .row_1_name{
        justify-content: flex-start;
        flex-direction: column;
    }
    .proigrvatel{
        height: 400px;
    }
}
@media (max-width: 650px) {
    .row_1_name{

    }
    .row_1{
        flex-direction: column;
        margin-bottom: 0;
    }
    .row_1 img{
        width: 100%;
    }
    .proigrvatel{
        height: 250px;
    }
}




