.Login{
    padding: 0 10px;

}

.login_con{
    max-width: 450px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 25px;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.LoginIn{
    display: flex;
    flex-direction: column;
}
.LoginIn input{
    border-left: none;
    border-right: none;
    border-top: none;
    border-bottom: 2px solid #0000FF;
    outline: none;
    margin-bottom: 10px;
    font-size: 22px;
}
.LoginIn span{
    font-size: 26px;
}

.LoginIn_Button{
    background-color: transparent;
    border: none;
    outline: none;
    background-image: linear-gradient(to right, #f9d423 0%, #ff4e50 100%);
    padding: 5px 0;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.3s ease;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}
.LoginIn_Button:hover{
    opacity: 0.9;
}
.login_con>span{
    text-align: center;
    margin-top: 10px;
    cursor: pointer;
    text-decoration: underline;
}

.error{
    border: 1px solid red;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 5px;
    color: red;
    font-size: 24px;
}
.error_x{
    cursor: pointer;
    font-size: 24px;
    text-transform: uppercase;
}

.varning{
    color: red;
    font-size: 24px;
}
