.page_1{
    margin: 0 auto;
    padding: 260px 10px 80px 10px;
    max-width: 1500px;
}

.carousel_item img{
    width: 175px;
    height: 250px;
}
.carousel_3{
    display: none;
}
.carousel_1{
    display: none;
}
.carousel_item{
    outline: none;
    display: flex!important;
    align-items: center;
    justify-content: center;
}
.page_2{
}
.page_2>p{
    background-image: linear-gradient(to right, #00dbde 0%, #fc00ff 100%);
    color: #FFF;
    font-size: 36px;
    padding: 5px;
}

.page_3>p{
    background-image: linear-gradient(to right, #00dbde 0%, #fc00ff 100%);
    color: #FFF;
    font-size: 36px;
    padding: 5px;
}
.page_4>p{
    background-image: linear-gradient(to right, #00dbde 0%, #fc00ff 100%);
    color: #FFF;
    font-size: 36px;
    padding: 5px;
}
.container{
    max-width: 1500px;
    margin: 0 auto;
}

.podborka{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 0 10px;
    margin: 30px 0;
}

.podborka_el img{
    height: 300px;
    border-radius: 25px;
    transition: all 0.3s ease;
}
.podborka_el img:hover{
    filter: brightness(40%);
}

.best{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 20px 0;
}
.NFFilms{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 20px 0;
    justify-content: center;
}

@media (max-width: 1350px) {
    .best{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}

@media (max-width: 1000px) {
    .carousel_5{
        display: none;
    }
    .carousel_3{
        display: block;
    }
    .podborka_el img{
        height: 200px;
    }
}
@media (max-width: 600px) {
    .carousel_1{
        display: block;
    }
    .carousel_3{
        display: none;
    }
}
@media (max-width: 700px) {
    .podborka{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .podborka_el img{
        height: 300px;
    }
    .podborka_el:not(:last-child){
        margin-bottom: 15px;
    }
}




